<script>
export default {
  data () {
    return {
      token: localStorage.getItem("token"),
      data:[],
      arr:[],
      diqu:'',
      city:'',
      ressadd:[],
      name:'',
      detailedaddress:''
    }
  },
  methods : {
    getCity (e) {
      var pid=e.target.value
      console.log(pid)
      var name=document.getElementById("a"+pid).innerText

      this.arr[0]=name
      console.log(this.arr)
      axios.post(this.url+'/api/show/area',{
        pid:pid
      })
        .then(response=> {
          this.city=response.data
        })
    },
    getDiqu:function (e) {
      var pid=e.target.value
      console.log(pid)
      var name=document.getElementById("a"+pid).innerText
      this.arr[1]=name
      console.log(this.arr)
      axios.post(this.url+'/api/show/area',{
        pid:pid
      })
        .then(response=> {
          this.diqu=response.data
        })
    },
    getDiqu1:function (e) {
      var pid=e.target.value
      console.log(pid)
      var name=document.getElementById("a"+pid).innerText
      this.arr[2]=name
      console.log(this.arr)

    },
    add_address () {

      var data=$('#data option:selected').html()

      var city=$('#city option:selected').html()

      var diqu=$('#diqu option:selected').html()

      var address=data+""+city+""+diqu

      this.ressadd=address
      axios.post(this.url+'/api/order/address',{
        address:this.ressadd,
        token:this.token,
        name:this.name,
        detailedaddress:this.detailedaddress
      })
        .then(res => {
          if (res.data.status=='ok'){
            alert(res.data.data)
          }
        })
    }
  },
  mounted: function () {
    axios.post(this.url+'/api/show/area',{
      pid:1
    })
      .then(res => {
        this.data = res.data
      })
  }
}
</script>



