<script>
export default {
  data () {
    return {
      filters: {
        id: 'aId'  //
      },
      msg:[],
      price:[],
      isajax:1,
      token: localStorage.getItem("token"),
      name: localStorage.getItem("name"),
      attr:[]
    }
  },
  mounted:function () {
    this.filters.id=this.$route.query.aId
    axios.post(this.url+'/api/show/product',{
      id: this.filters.id,
    })
      .then(response=> {
        this.msg=response.data
      })
  },
  methods : {
    getradio () {
      var  _this = this
      _this.attr=''
      $.each(this.msg.data,function (key,val){
        if ($("input[name='"+key+"']:checked").val()==undefined) {
          _this.isajax=0
          console.log('')
        }
      })
      if (this.isajax==1){
        var id=''
        var name=''

        $.each(this.msg.data,function (keys,value) {
          id=id+"-"+ $("input[name='"+keys+"']:checked").val()
          _this.attr=_this.attr +" "+$("input[name='"+keys+"']:checked").attr('name')+":"+ $("input[name='"+keys+"']:checked").attr('value1')
        })
        axios.post(this.url+'/api/show/shopping',{
          id:id,
          uid:this.$route.query.aId,
        })
          .then(response =>{
            this.price=response.data
          })
      }
      this.isajax = 1
    },
    shoppingcate (){
      var num=document.getElementById("a1").value

      axios.post(this.url+'/api/cate/shopping_cate',{
        num:num,
        goods_id:this.price[0].id,
        token:this.token,
        name: this.msg.name,
        attr_name:this.attr,
      })
    }
  }

}
</script>
