<script>
export default {
  data () {
    return {
      id: localStorage.getItem("id"),
      token: localStorage.getItem("token"),
      data:[],
      price:0,
      arr:[],
      oid:[]
    }
  },
  mounted() {
    axios.post(this.url + '/api/order/order', {
      id: this.id,
      token: this.token
    })
      .then(response => {
        this.data = response.data
        console.log(this.data)
        var toatlp = 0;
        this.data.forEach(function (val) {
          toatlp += val.number * val.price
        })
        this.price = toatlp
      })
    axios.post(this.url + '/api/order/ress', {
      token: this.token
    })
      .then(response => {
        this.arr = response.data
      })

  },
  methods : {
    addaction () {
      var id= "";
      for (var i = 0;i< this.data.length;i++) {
        id+=this.data[i].id+","
      }
      axios.post(this.url + '/api/order/addaction', {
        token: this.token,
        id:id,
        address:this.arr[0].detailedaddress
      })
        .then(response => {
          // console.log(response.data)
          // this.oid = response.data
          location.href="http://localhost/blog/public/api/pay/pay?token="+this.token+"&oid="+response.data+"&price="+this.price
          console.log(this.price)
        })
    }
  }
}
</script>



